@import url(https://fonts.googleapis.com/css?family=Archivo);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Header {     
  align-items: center;
  background: rgba(165, 165, 165, 0.68);
  display: flex;
  height: 100%;
  justify-content: space-around;
  margin: auto;
  padding: 0;
  text-align: center; 
}

.main-title {
  color: white;
  font-size: 26px;
}
  
.logo {
  height: 60px;
}

.back-btn {
  border: 1px solid black;
  border-radius: 20px;
  font-size: 16px;
  padding: 5px 10px;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}

.back-btn:hover {
  background: black;
  color: white;
  cursor: pointer;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}
.LocatingPopUp {
  align-items: center;
  border: 1px solid white;
  border-radius: 20px;
  background: grey;
  display: flex;
  justify-content: space-evenly;
  margin:  100px 20% 0 20%;
  opacity: .9;
  padding: 10px;
  width: 60%;
  z-index: 5;
}

.loading-gif {
  height: 40px;
}
.Start {
  align-items: center;
  background: url(/summit-register-FE/static/media/mountains-2.f57c3e0c.jpg) no-repeat center
    rgba(165, 165, 165, 0.68);
  background-size: auto 100%;
  display: flex;
  flex-direction: column;
}

.drop-down-controls {
  margin-top: 55vh;
  position: absolute;
}

.drop-down-menu {
  background: white;
  color: black;
  font-size: 16px;
  padding: 5px 10px;
}

.select-btn {
  border: 1px solid black;
  border-radius: 20px;
  font-size: 16px;
  margin-left: 5px;
  padding: 5px 10px;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}

.select-btn:hover,{
  background: black;
  color: white;
  cursor: pointer;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}
.HourlyForecast {
  align-items: center;
  background:white;
  border: 3px solid white;
  border-radius: 20px;
  color: black;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  margin: 0 10px;
  min-height: 40vh;
  min-width: 200px;
  overflow-x: hidden;
  overflow-y: auto;
  padding: 5px;
}

.weather-icon {
  height: 120px;
}

.HourlyForecast > h3 {
  font-size: 14px;
  margin: 2px;
}

.HourlyForecast > .time {
  border-bottom: 2px solid black;
  font-size: 20px;
}
.Info {
  align-items: center;
  background: url(/summit-register-FE/static/media/mountains-2.f57c3e0c.jpg) no-repeat center
  rgba(165, 165, 165, 0.68);
  background-size: auto 100%;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  overflow: auto;
}

.info-section {
  border: 1px solid white;
  border-radius: 20px;
  background: grey;
  display: flex;
  flex-direction: column;
  font-size: 18px;
  height: 70vh;
  justify-content: space-evenly;
  max-width: 80%;
  opacity: .9;
  overflow: auto;
  padding: 5px 20px;
  text-align: center;
}

.difficulty-icon {
  margin: 0 35%;
  width: 30%;
}

.display-text {
  line-height: 1.5;  
}

.weather-summary-text {
  margin: 15px;
}

.hourly-forecast-section{
  display: flex;  
  overflow: scroll;
}

.btn-container {
  display: flex;
  justify-content: space-evenly;
}

.view-log-btn,
.weather-btn {
  border: 1px solid black;
  border-radius: 20px;
  display: block;
  font-size: 16px;
  padding: 5px 10px;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
  min-width: 100px;
}

.view-log-btn:hover,
.weather-btn:hover {
  background: black;
  color: white;
  cursor: pointer;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}


.Log {
  background: url(/summit-register-FE/static/media/mountains-2.f57c3e0c.jpg) no-repeat center
  rgba(165, 165, 165, 0.68);
  background-size: auto 100%;
  color: white;
  overflow: scroll;
}

.comment-container {
  margin: 0 5%;
}

.comment {
  background: grey;
  border: 2px solid white;
  border-radius: 20px;
  margin: 10px auto;
  max-width: 60vw;
  opacity: .9;
  padding: 10px;
  text-align: center;
}

.empty-log-msg {
  border: 1px solid white;
  border-radius: 20px;
  background: grey;
  height: 10%;
  margin: 25vh auto 0 auto;
  opacity: .9;
  padding: 20px;
  text-align: center;
  width: 50%;
}

.user-image {
  height: 100px;
}

.SignIn {
  align-items: center;
  background: url(/summit-register-FE/static/media/mountains-2.f57c3e0c.jpg) no-repeat center
  rgba(165, 165, 165, 0.68);
  background-size: auto 100%;
  display: flex;
  justify-content: center;
}


.SignRegister {
  border: 1px solid black;
  border-radius: 20px;
  font-size: 16px;
  opacity: .6;
  padding: 5px 10px;
}

.active {
  background: rgb(1, 162, 1);
  color: white;
  opacity: 1;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}

.active:hover {
  background: white;
  color: rgb(1, 162, 1);
  cursor: pointer;
  opacity: 1;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}

.Footer {
  align-items: center;
  background: rgba(165, 165, 165, 0.68);
  display: flex;
  justify-content: space-around;
}

.footer-btn {
  border: 1px solid black;
  border-radius: 20px;
  font-size: 16px;
  padding: 5px 10px;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}

.footer-btn:hover{
  background: black;
  color: white;
  cursor: pointer;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}

.MyMountains {
  background: url(/summit-register-FE/static/media/mountains-2.f57c3e0c.jpg) no-repeat center
  rgba(165, 165, 165, 0.68);
  background-size: auto 100%;
  overflow: scroll;
}

.my-mountain {
  background: grey;
  border: 2px solid white;
  border-radius: 20px;
  color: white;
  margin: 10px auto;
  max-width: 50vw;
  opacity: .9;
  padding: 10px;
  text-align: center;
}

.no-registers-msg {
  border: 1px solid white;
  border-radius: 20px;
  background: grey;
  color: white;
  font-size: 18px;
  opacity: .9;
  margin: 30vh auto;
  padding: 5px 20px;
  text-align: center;
  width: 200px;
}

.LoadingScreen {
    align-items: center;
    background: url(/summit-register-FE/static/media/mountains-2.f57c3e0c.jpg) no-repeat center
    rgba(165, 165, 165, 0.68);
    background-size: auto 100%;
    display: flex;
    flex-direction: column;
    justify-content: center; 
  }

  .loading {
    height: 100px;
    width: 100px;
  }

.RegisterForm {
  align-items: center;
  background: url(/summit-register-FE/static/media/mountains-2.f57c3e0c.jpg) no-repeat center
  rgba(165, 165, 165, 0.68);
  background-size: auto 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  overflow: auto;
}

.register-form {
  align-items: center;
  border: 1px solid white;
  border-radius: 20px;
  background: grey;
  color: white;
  display: flex;
  flex-direction: column;
  opacity: .9;
  overflow: auto;
  padding: 30px;
  text-align: center;
}

.form-instructions {
  font-size: 18px;
}

.upload-instructions {
  font-size: 14px;
  margin: 10px;
}

.input {
  border: 1px solid black;
  border-radius: 10px;
  font-size: 14px;
  margin-left: 10px;
  padding: 5px;
}

.upload-image {
  border: 1px solid white;
  border-radius: 10px;
  padding: 5px;
}

.submit-btn {
  border: 1px solid black;
  border-radius: 20px;
  font-size: 16px;
  margin-top: 20px;
  padding: 5px 10px;
}

.submit-btn:hover {
  background: black;
  color: white;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}


* {
  font-family: "Archivo", sans-serif;
}

button {
  background: white;
  height: 36px;
  min-width: 70px;
}

.App {
  background: rgba(165, 165, 165);
  display: grid;
  grid-template-rows: 10vh 80vh 10vh;
  height: 100vh;
}

.Header {
  width: 100%;
}

